<template>
  <div>
    <Header />
    <my-swiper :banners="banners" />
    <Icons :icons="icons" />
    <Recommend />
    <HotSonger :artists="artists" />
  </div>
</template>

<script>
import Header from "./components/Header.vue";
import MySwiper from "./components/mySwiper.vue";
import Icons from "./components/Icons.vue";
import Recommend from "./components/recommend.vue";
import HotSonger from "./components/HotSonger.vue";

import {
  getIcons,
  getSwiper,
  getRecommend,
  getHotArtists,
} from "@/api/requestHome.js";

export default {
  name: "home",
  components: {
    Header,
    MySwiper,
    Icons,
    Recommend,
    HotSonger,
  },
  data() {
    return {
      icons: [], //首页圆形图标,
      banners: [], //轮播图数据
      HomeRecommend: { recommend: [] }, //推荐歌单数据,
      artists: [], //热门歌手
    };
  },
  //数据提供
  provide() {
    return {
      projectRecommend: this.HomeRecommend,
    };
  },
  methods: {
    //获取了轮播图数据
    async getBanner() {
      let { data } = await getSwiper();
      if (data.code === 200) {
        this.banners = data.banners;
      }
    },
    //获取icons图标列表数据
    async getIcons() {
      let { data } = await getIcons();
      if (data.code === 200) {
        this.icons = data.data;
      }
    },
    //获取推荐歌单数据
    async getRecommend() {
      let { data } = await getRecommend();
      if (data.code === 200) {
        this.HomeRecommend.recommend = data.result.splice(0, 6);
      }
    },
    //获取热门歌手数据
    async getHotArtists() {
      let { data } = await getHotArtists();
      if (data.code === 200) {
        this.artists = data.artists;
      }
    },
  },

  created() {
    //获取icons图标列表数据
    this.getIcons();
    //获取轮播图数据
    this.getBanner();
    //获取推荐歌单数据
    this.getRecommend();
    //获取热门歌手数据
    this.getHotArtists();
  },
};
</script>

