<template>
  <div class="player-content">
    <!-- mini-播放器 -->
    <div
      class="mini-player"
      v-show="$route.name === 'home'"
      @click="toggleShow(true)"
    >
      <!-- 歌曲信息 -->
      <div class="player-info">
        <div class="img-warp">
          <img :src="songImg" alt="" />
        </div>
        <div class="artist-info">
          <p class="name">{{ songName }}</p>
          <p class="song-name">
            <span v-for="(item, index) in songArtists" :key="index">{{
              item.name
            }}</span>
          </p>
        </div>
        <!-- icon 图标 -->
        <div class="icon-warp">
          <i class="iconfont" :class="playIcon" @click.stop="togglePlay"></i>
          <!-- <i class="iconfont icon-bofang1"></i> -->
        </div>
        <!-- <div class="mini-progress">32323</div> -->
      </div>
      <div class="mini-progress" :style="{ width: `${barPercent}` }"></div>
    </div>
    <!-- mini-播放器 =end-->

    <!-- scroll-player -->
    <transition name="player">
      <div
        class="player"
        v-show="fullScreen"
        :style="{ backgroundImage: `url(${songImg})` }"
      >
        <div class="player-mask"></div>
        <div class="top-info">
          <i
            class="iconfont icon-xiangxiajiantou"
            @click="toggleShow(false)"
          ></i>
          <div class="artist-info">
            <p class="music-name">{{ songName }}</p>

            <p class="song-name">
              <span v-for="(item, index) in songArtists" :key="index">{{
                item.name
              }}</span>
            </p>
          </div>
          <div class="kong"></div>
        </div>
        <!-- swiper -->
        <swiper ref="mySwiper" :options="swiperOptions">
          <swiper-slide class="img-container">
            <img :src="songImg" alt="" />
          </swiper-slide>
          <swiper-slide>
            <better-scroll
              :data="lyrictLines"
              ref="refScroll"
              class="betterScroll-warpper"
            >
              <div>
                <ul v-if="lyrictLines">
                  <li
                    ref="refli"
                    :class="{ active: index === currentlineNumber }"
                    v-for="(line, index) in lyrictLines"
                    :key="index"
                  >
                    {{ line.txt }}
                  </li>
                </ul>
                <div v-else>暂无歌词</div>
              </div>
            </better-scroll>
          </swiper-slide>
          <i
            :class="['iconfont', 'icon-aixin', { aixin: isLove }]"
            @click="addToLove()"
            >*</i
          >
          <i class="iconfont icon-pinglun" @click="goToComment"></i>
        </swiper>
        <!-- swiper end-->
        <div>
          <div class="progress">
            <span class="time">{{ formatTime(currentTime) }}</span>
            <div class="progress-bar" @click="progressClick" ref="progressBar">
              <div class="bar-moved" :style="{ width: `${barPercent}` }"></div>
              <div
                class="bar-btn"
                :style="{ left: `${barPercent}` }"
                @touchmove.prevent="progressMove"
                @touchend="progressEnd"
              ></div>
            </div>
            <span class="time">{{ formatTime(overTime) }}</span>
          </div>
          <div class="icon-info">
            <!-- 选择模式图标 -->
            <i class="iconfont ft-40" :class="modeIcon" @click="changMode"></i>
            <i class="iconfont icon-shangyishou ft-80" @click="prev"></i>
            <i
              class="iconfont ft-100"
              :class="playIcon"
              @click="togglePlay"
            ></i>
            <i class="iconfont icon-shangyishou-copy ft-80" @click="next"></i>
            <i class="iconfont icon-gengduo ft-40" @click="tooglePlayShow"></i>
          </div>
        </div>
      </div>
    </transition>
    <!-- scroll-player end -->
    <audio
      @ended="end"
      ref="audio"
      :src="musicData.url"
      @timeupdate="updataTime"
      autoplay
    ></audio>
    <!-- 遮罩层 -->
    <div
      class="play-list-mask"
      v-show="playListShow"
      @click.stop="tooglePlayShow"
    >
      <transition name="playlist">
        <better-scroll class="play-list-warper" v-show="playListShow">
          <ul>
            <li
              v-for="(item, index) in playList"
              :key="index"
              @click.stop="handleCurSongClick(index)"
            >
              <p class="song-name">{{ item.name }}</p>
              <div class="artist-name">
                -
                <p v-for="(arItem, indey) in item.ar" :key="indey">
                  {{ arItem.name }}
                </p>
              </div>
              <i
                class="iconfont icon-shanchu"
                @click.stop="delCurSong(item.id)"
              ></i>
            </li>
          </ul>
        </better-scroll>
      </transition>
    </div>
    <!-- 遮罩层-end -->
  </div>
</template>

<script>
import BetterScroll from "@/components/BetterScroll.vue";

import { mapMutations, mapGetters } from "vuex";
import { getCurMusicUrl, getLyric } from "../api/requestHome";
import { playMode } from "../common/js/aliasConfig";
export default {
  name: "player",
  components: {
    BetterScroll,
  },
  data() {
    return {
      swiperOptions: {
        pagination: {
          el: ".swiper-pagination", //与slot="pagination"处 class 一致
          clickable: true, //轮播按钮支持点击
          loop: true,
        },
      },
      musicData: {},
      lyricData: null,
      playing: false,
      currentlineNumber: 0,
      currentTime: 0,
      overTime: 0,
      touchBarWillMove: false,
      lyrictLines: [],
      playListShow: false,
    };
  },
  computed: {
    ...mapGetters([
      "currentIndex",
      "fullScreen",
      "mode",
      "sequencesList",
      "currentSong",
      "playList",
      "miniPlayerHild",
      "isLove"
    ]),
    songName() {
      return this.currentSong ? this.currentSong.name : JSON.parse(localStorage.getItem('HistoryList'))[0].name ;
    },
    songArtists() {
      return this.currentSong ? this.currentSong.ar :  JSON.parse(localStorage.getItem('HistoryList'))[0].ar;
    },
    songImg() {
      return this.currentSong
        ? `${this.currentSong.al.picUrl}?param=200x200`
        : "";
    },
    //播放图标
    playIcon() {
      return this.playing ? "icon-bofang1" : "icon-bofang";
    },
    //选择模式图标
    modeIcon() {
      return this.mode == playMode.random
        ? "icon-suijibofang" // 顺序播放 / 默认播放
        : this.mode === playMode.sequence
        ? "icon-24gl-repeat2" //重复播放
        : "icon-xunhuanbofang";//循环播放
    },

    barPercent() {
      let p = this.currentTime / this.overTime;
      if (p === 0) {
        return 0;
      }
      p = Number(p * 100).toFixed();
      return `${p}%`;
    },
  },
  methods: {
    ...mapMutations([
      "SET_CURRENT_INDEX",
      "SET_FULLSCREEN",
      "SET_CURRENT_INDEX",
      "SET_MODE",
      "SET_PLAY_LIST",
      "SET_MINIPLAYERHILD",
      "DEL_CURRINDEX_SONG",
      "SET_LOVE_lIST",
      "DEL_FROM_LOVE_LIST",
      "SET_HISTORY_LIST"
    ]),
    toggleShow(val) {
      this.SET_FULLSCREEN(val);
    },
    //获取当前音乐url
    async getCurMusicUrl(id) {
      let { data } = await getCurMusicUrl({
        id,
      });
      if (data.code == 200 && data.data[0].code === 200) {
        this.musicData = data.data[0];
        this.$nextTick(() => {
          this.togglePlay(true)
          this.SET_HISTORY_LIST(this.currentSong)
          // this.playing = true;

        });
      }
    },
    //获取当前音乐歌词
    async getCurLyric(id) {
      let { data } = await getLyric({
        id,
      });
      if (data.code === 200) {
        this.lyricData = data.lrc.lyric;
        this.initLines();
      }
    },
    // 切换播放按钮
    togglePlay(val) {
      if (!this.currentSong) return false;
      const audio = this.$refs.audio;

      if (val === true || val === false) {
        this.playing = val;
      } else {
        this.playing = !this.playing;
      }
      if (this.playing) {
        audio.play();
      } else {
        audio.pause();
      }
    },
    //切换上一首歌
    prev() {
      let len = this.playList.length;
      let newIndex = this.currentIndex - 1;
      if (newIndex < 0) {
        newIndex = len - 1;
      }
      this.SET_CURRENT_INDEX(newIndex);
    },
    //切换下一首歌
    next() {
      let len = this.playList.length;
      let newIndex = this.currentIndex + 1;
      if (newIndex === len) {
        newIndex = 0;
      }
      this.SET_CURRENT_INDEX(newIndex);
    },
    //改变模式
    changMode() {
      //得到切换的模式的值
      let modeNumber = (this.mode + 1) % 3;
      //设置切换的模式的值
      this.SET_MODE(modeNumber);
      let newPlayList = [];
      if (this.mode === playMode.random) {
        newPlayList = this.getRandomList(this.sequencesList);
      } else {
        //将乱序的歌曲列表赋值给newPlayList
        newPlayList = this.sequencesList;
      }
      //找到当前歌曲在歌曲列表的索引
      let newCurIndex = newPlayList.findIndex(
        (item) => item.id === this.currentSong.id
      );
      this.SET_PLAY_LIST(newPlayList);
      this.SET_CURRENT_INDEX(newCurIndex);
    },
    //得到打乱的歌曲列表
    getRandomList(val) {
      let newArr = [].concat(val);
      return newArr.sort((a, b) => (Math.random() > 0.5 ? -1 : 1));
    },
    //循环播放
    loop() {
      const audio = this.$refs.audio;
      audio.currentTime = 0;
      audio.play();
    },
    //歌曲播放结束
    end() {
      if (this.mode === playMode.loop) {
        this.loop();
      } else {
        this.next();
      }
    },
    //当播放时间发生变化时触发
    updataTime(e) {
      if (!this.touchBarWillMove) {
        this.currentTime = e.target.currentTime;
        this.overTime = e.target.duration;
      }
      if (this.lyricData) {
        this.moveLyric();
      }
    },
    //格式化歌曲时间
    formatTime(val) {
      if (isNaN(val)) return `00:00`;
      let m = Math.floor(val / 60);
      let s = Math.floor(val % 60);
      if (m < 10) {
        m = `0${m}`;
      }
      if (s < 10) {
        s = `0${s}`;
      }
      return `${m}:${s}`;
    },
    //当移动小原点时，执行的函数
    progressMove(e) {
      this.touchBarWillMove = true;
      const pageX = e.touches[0].pageX;
      this.calcPercent(pageX);
    },
    //计算移动了多少距离
    calcPercent(x) {
      // 进度条左边距离最左侧的距离
      let offsetLeft = this.$refs.progressBar.offsetLeft;
      //计算进度条的宽度
      let barWidth = this.$refs.progressBar.clientWidth;
      //计算出移动的距离
      let moveWidth = x - offsetLeft;
      //边界处理
      if (moveWidth > barWidth) {
        moveWidth = barWidth;
      }
      if (moveWidth < 0) {
        moveWidth = 0;
      }
      //算出移动的宽度的百分比
      let p = moveWidth / barWidth;
      // 把计算的当前歌曲时间赋值给currentTime
      this.currentTime = this.overTime * p;
    },
    //当触摸放下的时候触发
    progressEnd() {
      this.touchBarWillMove = false;
      this.resetPlay();
      if (this.lyricData) {
        this.moveLyric();
      }
    },
    //重新设置播放的currentTime,并播放
    resetPlay() {
      // 把计算的歌曲时间赋值给播放实例
      this.$refs.audio.currentTime = this.currentTime;
      this.togglePlay(true);
    },
    //点击bar-btn，小圆点移动到点击位置
    progressClick(e) {
      // this.touchBarWillMove = true;
      let pagex = e.pageX;
      this.calcPercent(pagex);
      this.resetPlay();
      this.moveLyric();
    },
    initLines() {
      this.lyrictLines = [];
      if (this.lyricData) {
        const lines = this.lyricData.split("\n");
        for (let i = 0; i < lines.length; i++) {
          const line = lines[i];
          const timeExp = /\[(\d{2}):(\d{2}\.\d{2,3})\]/g;
          const result = timeExp.exec(line);
          if (result) {
            //time 转化成毫秒
            const time =
              Number(result[1] * 60 * 1000) + Number(result[2] * 1000);
            const txt = line.replace(timeExp, "").trim();
            this.lyrictLines.push({
              time,
              txt,
            });
          }
        }
      }
    },
    moveLyric() {
      //this.currentTime 秒
      this.currentlineNumber = this.findCurrentNumber(this.currentTime * 1000);
      if (this.currentlineNumber > 6) {
        this.$refs.refScroll.scrollToElement(
          this.$refs.refli[this.currentlineNumber - 10]
        );
      } else {
        this.$refs.refScroll.scrollTo(0, 0, 1000);
      }
    },
    findCurrentNumber(time) {
      for (let i = 0; i < this.lyrictLines.length; i++) {
        if (time < this.lyrictLines[i].time) {
          return i - 1;
        }
      }
      return this.lyrictLines.length - 1;
    },
    //跳转到评论页
    goToComment() {
      this.$router.push({
        name: "MusicComment",
        params: {
          id: this.currentSong.id,
        },
      });
      this.SET_FULLSCREEN(false);
    },
    //切换playList遮罩层
    tooglePlayShow() {
      this.playListShow = !this.playListShow;
    },
    handleCurSongClick(curIndex) {
      this.SET_CURRENT_INDEX(curIndex);
      this.tooglePlayShow();
    },
    //删除当前歌曲
    delCurSong(id) {
      if (this.playList.length === 1) {
        this.playListShow = false;
        this.SET_FULLSCREEN(false);
        this.togglePlay(false);
        this.SET_PLAY_LIST([]);
        return false;
      }
      this.DEL_CURRINDEX_SONG(id);
    },
     //点击收藏图标触发事件
    addToLove(){
      if(this.isLove){
        this.DEL_FROM_LOVE_LIST(this.currentSong);
        return  false;
      }
      this.SET_LOVE_lIST(this.currentSong)
    }
  },

  watch: {
    //监听currentSong的变化才去更改发送请求
    currentSong(newVal, oldVal) {
      if (this.oldVal === undefined || newVal.id !== oldVal.id) {
        this.getCurMusicUrl(newVal.id);
        this.getCurLyric(newVal.id);
      }
    },
    //监听到当前页面是home页面才显示minni播放器
    $route(newVal, oldVal) {
      if (newVal.name == "home") {
        this.SET_MINIPLAYERHILD(true);
      } else {
        this.SET_MINIPLAYERHILD(false);
      }
    },
   
  },
};
</script>

<style lang="less" scoped>
.betterScroll-warpper {
  width: 100%;
  height: 100%;
  overflow: hidden;
  color: white;

  ul {
    padding: 30px 20px;
    width: 100%;
    height: 100%;
    text-align: center;

    li {
      line-height: 1.5;
      min-height: 50px;
      font-size: 28px;
      opacity: 0.5;

      &.active {
        opacity: 1;
        font-weight: 800;
      }
    }
  }
}

.player-content {
  .mini-player {
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    background: #150a06;
    // background: whitesmoke;
    z-index: 999;
    color: white;
    padding: 20px 30px;
    height: 130px;
    box-sizing: border-box;

    .player-info {
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: space-between;

      .img-warp {
        width: 100px;
        height: 100px;
        border-radius: 100%;
        overflow: hidden;

        img {
          width: 100%;
          height: 100%;
          background-size: 100% 100%;
        }
      }

      .artist-info {
        flex: 0.7;
        margin-left: 5px;

        .name {
          font-size: 20px;
          margin-bottom: 16px;
        }

        .song-name {
          font-size: 20px;

          span {
            margin-right: 20px;
          }
        }
      }

      .icon-warp {
        i {
          font-size: 80px;
        }
      }
    }
  }

  .mini-progress {
    height: 6px;
    background-color: #f2353c;
    position: absolute;
    bottom: 0;
    left: 0;
  }

  .player-mask {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: black;
    opacity: 0.5;
    z-index: -1;
  }

  .player {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 999;
    background-image: url("../assets/zjl.jpeg");
    background-size: cover;
    background-position: center center;
    background-repeat: no-repeat;
    overflow: hidden;
    color: white;

    &:after {
      content: "";
      background: inherit;
      position: absolute;
      top: -40px;
      left: -40px;
      width: calc(100% + 80px);
      height: calc(100% + 80px);
      filter: blur(15px);
      z-index: -2;
    }

    .top-info {
      padding: 30px;
      display: flex;
      justify-content: space-between;
      align-content: center;

      i {
        font-size: 46px;
      }

      .artist-info {
        text-overflow: ellipsis;
        font-size: 18px;
        margin-top: 12px;
        color: #b2b2b2;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        .music-name {
          margin-bottom: 10px;
          font-size: 35px;
        }

        .song-name {
          span {
            margin-right: 10px;
          }
        }
      }
    }

    .swiper-container {
      position: relative;
      width: 100%;
      height: calc(100% - 400px);

      .icon-aixin {
        position: absolute;
        left: 30px;
        bottom: 30px;
        color: #c4b9bb;
        font-size: 50px;
      }

      .icon-pinglun {
        position: absolute;
        left: 100px;

        bottom: 33px;
        color: #c4b9bb;
        font-size: 40px;
      }

      .img-container {
        display: flex;
        justify-content: center;
        align-items: center;

        img {
          width: 600px;
          height: 600px;
          border-radius: 50%;
          border: 20px solid #2b433c;
        }
      }
    }

    .progress {
      height: 50px;
      padding: 30px;
      display: flex;
      justify-content: center;
      align-items: center;

      .time {
        width: 60px;
        font-size: 30px;
        margin: 0 50px;
      }

      .progress-bar {
        position: relative;
        width: 560px;
        height: 4px;
        background: #7e7374;
        margin: 0 30px;

        .bar-moved {
          position: absolute;
          left: 0;
          top: 0;
          height: 4px;
          background: #f2353c;
          width: 30px;
        }

        .bar-btn {
          position: absolute;

          top: 0;
          width: 20px;
          height: 20px;
          border-radius: 50%;
          background-color: white;
          border: 5px solid #5d5759;
          transform: translate3d(-50%, -50%, 0);
        }
      }
    }

    .icon-info {
      padding: 30px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      color: #c4b9bb;
      z-index: 999;

      .ft-40 {
        font-size: 60px;
      }

      .ft-80 {
        font-size: 80px;
      }

      .ft-100 {
        font-size: 100px;
      }

      i {
        font-size: 80px;
      }
    }
  }
}

.player-enter-active,
.player-leave-active {
  transition: all 0.3s;
  opacity: 1;

  .top-info,
  .icon-info {
    transform: translate3d(0, 0, 0);
    transition: all 0.3s;
  }
}

.player-enter,
.player-leave-to {
  opacity: 0;

  .top-info {
    transform: translate3d(0, -100px, 0);
  }

  .icon-info {
    transform: translate3d(0, 100px, 0);
  }
}

// 弹出播放列表
.play-list-mask {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.7);
  z-index: 999;
}
.play-list-warper {
  width: 100%;
  position: fixed;
  bottom: 0;
  left: 0;
  height: 800px;
  overflow: hidden;
  z-index: 9999;
  background-color: rgba(255, 255, 255, 0.9);
  ul {
    padding-bottom: 80px;
    li {
      padding: 30px 20px;
      height: 100%;
      border-bottom: 1px solid #e6e6e6;
      display: flex;
      justify-content: space-between;
      align-items: center;
      p {
        margin-right: 10px;
      }
      .song-name {
        font-size: 30px;
        margin-right: 10px;
      }
      .artist-name {
        flex: 1;
        display: flex;
        align-self: flex-end;
        align-items: flex-end;
        color: #ccc;
        p {
          font-size: 6px;
          color: #ccc;
        }
      }
      i {
        font-size: 50px;
      }
    }
  }
}
.playlist-enter-active,
.playlist-leave-active {
  transition: all 0.3s ease-in-out;
}
.playlist-enter,
.playlist-leave-to {
  transform: translate3d(0, 100%, 0);
}
.aixin {
  color: yellow !important;
}
</style>
