<template>
  <div class="rank-content">
    <m-header>排行榜</m-header>
    <div>
      <!-- 官方榜 -->
      <div>
        <p class="rank-title">官方榜</p>
        <ul class="rank-info">
          <li
            class="margin-li"
            v-for="(item, index) in officeList"
            :key="index"
            @click="goToRankInfo(item)"
          >
            <img :src="`${item.coverImgUrl}?param=200y200`" alt="" />
            <div>
              <ul>
                <li
                  class="li-text"
                  v-for="(itemList, indey) in item.tracks"
                  :key="indey"
                >
                  {{ indey + 1 }}、{{ itemList.first }}-{{ itemList.second }}
                </li>
              </ul>
            </div>
          </li>
        </ul>
      </div>
      <!-- 推荐榜 -->
      <div>
        <p class="rank-title">推荐榜</p>
        <ul class="recommend-list">
          <li
            v-for="(item, index) in recommendList"
            :key="index"
            @click="goToRankInfo(item)"
          >
            <img v-lazy="`${item.coverImgUrl}?param=400y400`" alt="" />
            <p>{{ item.name }}</p>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import mHeader from "@/components/mHeader.vue";
import { getToplist } from "@/api/requestHome.js";
import BetterScroll from "@/components/BetterScroll.vue";

export default {
  name: "rank",
  components: { mHeader, BetterScroll },
  data() {
    return {
      officeList: [],
      recommendList: [],
      allData: [],
    };
  },
  async created() {
    const { data } = await getToplist();
    if (data.code === 200) {
      this.allData = data.list;
      this.officeList = data.list.slice(0, 4);
      this.recommendList = data.list.slice(4);
    }
  },

  methods: {
    goToRankInfo(curItem) {
      console.log(curItem.id);
      this.$router.push({
        name: "RankInfo",
        params: {
          id: curItem.id,
        },
      });
    },
  },
};
</script>

