import Vue from 'vue'
import VueRouter from 'vue-router'
import home from '@/views/home'
import parent from '@/views/text/Parent'
import playListView from '@/views/playListView'
import rank from '@/views/rank'
import recommendView from '@/views/recommendView'
import artists from '@/views/artists'

import ArtistInfo from '@/views/ArtistInfo.vue'
import PlayListInfo from '../views/PlayListInfo.vue'
import RankInfo from '../views/RankInfo.vue'


import Search from '../views/search'




import MusicComment from '../views/MusicComment/index.vue'



Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    component: home,
    name: 'home'
  },
  {
    path: '/home',
    component: home,
    name: 'home'
  },
  {
    path: '/playListView',
    component: playListView,
    name: 'playListView'
  },
  {
    path: '/recommendView',
    component: recommendView,
    name: 'recommendView'
  },
  {
    path: '/artists',
    component: artists,
    name: 'artists'
  },
  {
    path: '/rank',
    component: rank,
    name: 'rank'
  },
  {
    path: '/text',
    component: parent,
    name: 'parent'
  },
  {
    path: '/ArtistInfo/:id',
    component: ArtistInfo,
    name: 'ArtistInfo'
  },
  {
    path: '/PlayListInfo/:id',
    component: PlayListInfo,
    name: 'PlayListInfo'
  },
  {
    path: '/RankInfo/:id',
    component: RankInfo,
    name: 'RankInfo'
  },
  {
    path: '/MusicComment/:id',
    component: MusicComment,
    name: 'MusicComment'
  },
  {
    path: '/search',
    component: Search,
    name: 'Search'
  }

]


const router = new VueRouter({
  // mode: 'history',
  base: process.env.BASE_URL,
  routes
})
export default router;
