<template>
  <div class="rank-info">
    <top-nav :imgUrl="imgUrl" :name="name"></top-nav>
    <better-scroll
      ref="refScroll"
      :data="playDetailData"
      class="betterScroll-warpper"
    >
      <song-list :SongList="playDetailData" @clickItem="addToPlay"></song-list>
    </better-scroll>
  </div>
</template>

<script>
import miXins from "@/common/js/miXins.js";
import {foramtData} from "@/common/js/utils";
import {getCurArtist} from "@/api/requestHome";
import {mapMutations} from "vuex";

export default {
  name: "artist-info",
  mixins: [miXins],
  data() {
    return {
      playDetailData: [],
    };
  },

  async created() {
    const {data} = await getCurArtist({id: this.$route.params.id});
    if (data.code === 200) {
      this.playDetailData = foramtData(data.songs);
    }
  },


  methods: {
    ...mapMutations(['SET_FULLSCREEN', 'SET_PLAY_LIST', 'SET_SEQUENCE_LIST', 'SET_CURRENT_INDEX', 'SET_MODE']),
    addToPlay(curItem, curIndex) {
      console.log(curItem, curIndex)
      this.SET_FULLSCREEN(true)
      this.SET_CURRENT_INDEX(curIndex);
      this.SET_PLAY_LIST(this.playDetailData);
      this.SET_SEQUENCE_LIST(this.playDetailData)

    }
  }
};
</script>
<style lang="less" scoped>
.betterScroll-warpper {
  height: calc(100vh - 580px);
  overflow: hidden;
}
</style>
