<template>
  <div>
    <m-header>全部歌手👨‍🎤</m-header>
    <div class="select-songer">
      <div class="area">
        <span
          @click="handleSelectCurArea(item.area, index)"
          v-for="(item, index) in area"
          :class="{ isActive: index === curIndex }"
          :key="item.type"
          >{{ item.title }}</span
        >
      </div>
      <div class="sex">
        <span
          @click="handleSelectSex(index)"
          v-for="(item, index) in sexlist"
          :key="index"
          :class="{ isActive: index === sexCurIndex }"
          >{{ item.sex }}</span
        >
      </div>
    </div>
    <p class="title">热门歌手👩‍🎤</p>
    <better-scroll ref="refScroll" :data="artistsData" class="bs-warper">
      <artist-list :artists="artistsData" />
    </better-scroll>
  </div>
</template>

<script>
import mHeader from "@/components/mHeader.vue";
import ArtistList from "@/components/ArtistList.vue";
import BetterScroll from "@/components/BetterScroll.vue";

import { getHotArtists, getSelectArtists } from "@/api/requestHome.js";

export default {
  name: "artists",
  components: {
    ArtistList,
    mHeader,
    BetterScroll,
  },
  data() {
    return {
      artistsData: [],
      area: [
        { title: "华语", area: 7 },
        { title: "欧美", area: 96 },
        { title: "日本", area: 8 },
        { title: "韩国", area: 16 },
        { title: "其他", area: 0 },
      ],
      sexlist: [{ sex: "男" }, { sex: "女" }, { sex: "乐队/组合" }],
      curIndex: -1,
      sexCurIndex: -1,
      params: { offset: 0, limit: 10, area: -1, type: -1 }, //请求携带的参数
    };
  },
  mounted() {
    this.$nextTick(() => {
      this.$refs.refScroll.scroll.on("pullingUp", async () => {
        this.params.offset += 11;
        const { data } = await getSelectArtists(this.params);
        if (data.artists.length === 0) {
          console.log("没有数据了");
          return false;
        }
        const result = data.artists;

        this.artistsData.push(...result);
        // 事情做完，需要调用此方法告诉 better-scroll 数据已加载，否则上拉事件只会执行一次

        // this.scroll.finishPullDown()
      });
    });
  },

  created() {
    this.getSelectArtists(this.params);
  },

  methods: {
    async getArtists() {
      const { data } = await getHotArtists(this.params);
      if (data.code === 200) {
        this.artistsData = data.artists;
      }
    },
    //请求方法
    async getSelectArtists(params) {
      const { data } = await getSelectArtists(params);
      if (data.code === 200) {
        this.artistsData = data.artists;
      }
    },
    //点击area 华语 欧美 日本
    // scrollTo(x, y, time, easing)
    handleSelectCurArea(area, index) {
      this.$refs.refScroll.scroll.scrollTo(0, 0, 1000);

      this.params.offset = 0;
      if (this.sexCurIndex === -1) {
        this.sexCurIndex = 0;
        this.params.type=1
      }
      this.params.offset = 0;
      this.curIndex = index;
      this.params.area = area;
      this.getSelectArtists(this.params);
    },
    //点击 男 女 乐队组合触发事件
    handleSelectSex(curIndex) {
      this.$refs.refScroll.scroll.scrollTo(0, 0, 1000);

      if (this.curIndex == -1) {
        this.curIndex = 0;
      }
      this.sexCurIndex = curIndex;
      this.params.type = curIndex + 1;
      this.getSelectArtists(this.params);
    },
  },
};
</script>

