  .area {
    padding: 50px 40px 20px 40px;
    span {
      margin-right: 80px;
      font-size: 38px;
      line-height: 40px;
      text-align: center;
      &.isActive {
        color: red;
      }
    }
  }
  .sex {
    padding: 20px 40px;
    span {
      margin-right: 80px;
      font-size: 38px;
      line-height: 40px;
      &.isActive {
