import SongList from "@/components/SongList";
import TopNav from "@/components/TopNav.vue";
import BetterScroll from "@/components/BetterScroll.vue";
import {mapMutations} from 'vuex'

export default {
    components: {
        SongList,
        TopNav,
        BetterScroll
    },
    data() {
        return {
          playDetailData: [],
        };
      },
      methods:{
        ...mapMutations(['SET_FULLSCREEN', 'SET_PLAY_LIST', 'SET_SEQUENCE_LIST', 'SET_CURRENT_INDEX', 'SET_MODE']),
        addToPlay(curItem, curIndex) {
          this.SET_FULLSCREEN(true)
          this.SET_CURRENT_INDEX(curIndex);
          this.SET_PLAY_LIST(JSON.parse(JSON.stringify(this.playDetailData)));
          this.SET_SEQUENCE_LIST(JSON.parse(JSON.stringify(this.playDetailData)) )}
      },

    computed: {
        imgUrl() {
            if (this.playDetailData.length > 0) {
                return this.playDetailData[0].al.picUrl;
            }
            return "";
        },
        name() {
            if (this.playDetailData.length > 0) {
                return this.playDetailData[0].al.name;
            }
            return "暂无数据";
        }
    },



};
