<template>
  <div v-show="len > 0" @click="handleCurAnswerClick" class="curAnswer">
    {{ len }} 条回复 >
  </div>
</template>

<script>
import { getCurFloorComment } from "@/api/requestHome";
export default {
  props: {
    parentCommentId: {
      type: Number,
    },
  },

  inject: ["id"],
  data() {
    return {
      len: 0,
      commentsData: [],
    };
  },
  methods: {
    handleCurAnswerClick() {
      this.$emit("emitCurrentAnswer", {
        commentsData: this.commentsData,
        parentCommentId: this.parentCommentId,
        len: this.len,
      });
    },
  },

  async mounted() {
    this.$nextTick(async () => {
      let params = {
        parentCommentId: this.parentCommentId,
        id: this.id,
        type: 0,
      };
      const { data } = await getCurFloorComment(params);
      this.len = data.data.totalCount;
      //   console.log(data);
      this.commentsData = data.data.comments;
    });
  },
};
</script>

<style lang="less" scoped>
.curAnswer {
  color: rgba(13, 93, 197, 0.829);
  font-size: 20px;
  margin-top: 20px;
}
</style>
