<template>
  <div class="song-list">
    <top-nav
      :imgUrl="imgUrl"
      :name="name"
    ></top-nav>
    <better-scroll :data="playDetailData" class="betterScroll-warpper">
      <song-list @clickItem='addToPlay' :SongList="playDetailData"></song-list>
    </better-scroll>
  </div>
</template>

<script>
import { getPlayDetail } from "../api/requestHome";
import BetterScroll from "@/components/BetterScroll.vue";
import SongList from "@/components/SongList";


import TopNav from "@/components/TopNav.vue";
import { foramtData } from "../common/js/utils";

import miXins from '@/common/js/miXins.js'
export default {
  name: "play-list-detail",
  components: {
    SongList,
    TopNav,
    BetterScroll
  },
  mixins:[miXins],
  computed: {
    imgUrl() {
      if (this.playDetailData.length > 0) {
        return this.playDetailData[0].al.picUrl;
      }
      return "";
    },
    name() {
      if (this.playDetailData.length > 0) {
        return this.playDetailData[0].al.name;
      }
      return "暂无数据";
    }
  },
  data() {
    return {
      playDetailData: [],
    };
  },
  async created() {
    console.log(this.$route.params);
    const { data } = await getPlayDetail({ id: this.$route.params.id ,limit:30});
    this.playDetailData = foramtData(data.playlist.tracks);

    if (data.code === 200) {
      console.log(data);
    }
  },

};
</script>

