      <li v-for="(item) in artists" :key="item.id" @click="goToCurArtistInfo(item.id)">
        <img v-lazy="`${item.picUrl}?param=70y70`" alt="" />
        <div class="info">
          <div class="name">{{ item.name }}</div>
          <div class="dec">
            有{{ item.albumSize }}专辑 ，共{{ item.musicSize }}首歌
          </div>
        </div>
      </li>
    </ul>
</template>

<script>
export default {
  name: "artist-list",
  props: {
    artists: {
      type: Array,
      default() {
        return [];
      },
    },
  },
  methods:{
