<template>
  <div class="play-list-content">
    <ul class="play-list">
      <li
        v-for="(curItem, index) in projectRecommend.recommend"
        :key="index"
        @click="handleCuritemClick(curItem)"
      >
        <div class="img-warp">
          <div class="play-count">{{ calcPlaycount(curItem.playCount) }}</div>
          <img v-lazy="`${curItem.picUrl}?param=200x200`" alt="" />
          <i class="iconfont icon-bofang"></i>
        </div>
        <div class="name">{{ curItem.name }}</div>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  inject: ["projectRecommend"],
  computed: {
    calcPlaycount() {
      return (curCount) => {
        let calcAforeCount = null;
        calcAforeCount = (curCount / 10000).toFixed(0);
        if (calcAforeCount < 1) {
          return curCount;
        }

        return calcAforeCount + "万";
      };
    },
  },
  methods: {
    handleCuritemClick(curItem) {
      this.$router.push({
        name:'PlayListInfo',
        params:{
          id:curItem.id
        }
      })
    }, 
  },
};
</script>

