<template>
  <div>
    <top-nav :imgUrl="imgUrl" :name="name"></top-nav>
    <!-- <better-scroll :data="newSongerData" class="betterScroll-warpper"> -->
    <song-list @clickItem="addToPlay" :SongList="playDetailData"> </song-list>
    <!-- </better-scroll> -->
  </div>
</template>

<script>
import { getNewSong } from "@/api/requestHome.js";
import { foramtData } from "../../common/js/utils";
import TopNav from "@/components/TopNav.vue";
import SongList from "@/components/SongList";
import miXins from "@/common/js/miXins.js";
// import BetterScroll from "@/components/BetterScroll.vue";

export default {
  components: {
    TopNav,
    SongList,
  },
  mixins: [miXins],
  data() {
    return {
      playDetailData: [],
    };
  },
  methods: {
    handleClick(curItem, index) {
      console.log(curItem, index);
    },
  },
  computed: {
    imgUrl() {
      if (this.playDetailData.length > 0) {
        return this.playDetailData[0].al.picUrl;
      }
      return "";
    },
    name() {
      if (this.playDetailData.length > 0) {
        return this.playDetailData[0].al.name;
      }
      return "暂无数据";
    },
  },
  async created() {
    const { data } = await getNewSong();

    if (data.code === 200) {
      this.playDetailData = foramtData(data.result);
    }
  },
};
</script>

<style lang="less" scoped>
.betterScroll-warpper {
  height: calc(100vh - 500px);
  overflow: hidden;
}
</style>
