<template>
  <div>
    <NavBar>
      <i slot='left' class="iconfont  icon-xitongfanhui" @click="handleIconClick"></i>
      <span slot='center'>评论{{ commentData.total }}</span>
    </NavBar>
    <!-- <m-header v-if="commentData">评论{{ commentData.total }}</m-header> -->
    <div class="select-comment">
      <div class="title">评论区</div>
      <div class="select-option">
        <span :class="{ active: 0 === curIndex }" @click="handleClick(0)"
          >推荐</span
        >
        <span :class="{ active: 1 === curIndex }" @click="handleClick(1)"
          >最热</span
        >
      </div>
    </div>
    <better-scroll
      ref="refScroll"
      :data="commentData"
      class="betterScroll-warpper"
    >
      <comment-info
        :commentData="commentData"
        @emitFloorData="hasShowFloor"
        @CurCommitInfo='CurCommitInfo'
      ></comment-info>
    </better-scroll>

    <!-- floor -->

    <div class="floor-content" v-show="isFloor" @click="offFloor">
      <transition name="fade" :duration="500">
        <div
          class="floor-warp"
          v-show="isFloor"
          @click.stop="handleStop($event)"
        >
      <div class="floor-header">
        <i  class="iconfont icon-xitongfanhui"></i>
        <div> {{ answerCount }} 条回复</div>
        <div></div>
      </div>
          <better-scroll
            ref="refFloorScroll"
            :data="floorData"
            class="betterScroll-floor-warpper"
          >
            <comment-info
              :commentData="floorData"
              @emitFloorData="hasShowFloor($event)"
              class="comment-info"
            ></comment-info>
          </better-scroll>
        </div>
      </transition>
    </div>

    <!-- floor - end -->
    <!-- 发送评论回复框 -->
  <div class="send-wawrp">
      <input type="text" :placeholder="CurInfoName" v-model='CurInfoName' >
      <i class="iconfont icon-fasong" @click="handleSendComment"></i>
    </div>
  </div>
</template>

<script>
import { getMusicComment, getCurFloorComment ,sendComment} from "@/api/requestHome";
import CommentInfo from "./components/CommentInfo.vue";
import mHeader from "@/components/mHeader.vue";
import BetterScroll from "@/components/BetterScroll.vue";
import NavBar from '@/components/NavBar.vue'

export default {
  name: "music-comment",
  data() {
    return {
      commentData: [],
      recommendCommentData: [],
      hotCommentData: [],
      floorData: [],
      curIndex: 0,
      isFloor: false,
      isFloorWarp: false,
      offset: 6,
      answerCount: 0,
      //获取评论的参数
      params: {
        type: 0,
        time: null,
        parentCommentId: null,
        id: 0,
      },
      //当前需要发送的评论参数
      commentParams:{
        type:0,
        content:'',
        commentId:null,
        id:null,
        t:1
      },
      CurInfoName:''
    };
  },
  mounted() {
    this.$nextTick(() => {
      this.$refs.refScroll.scroll.on("pullingUp", async () => {
        if (this.curIndex === 0) {
          //请求数据
          const data = await getMusicComment({
            offset: this.offset,
            id: this.$route.params.id,
            limit: 10,
          });
          this.offset = this.offset + 11;

          //修改数据截取位置
          //添加数据
          this.recommendCommentData.push(...data.data.comments);
          //   this.commentData.push(...data.data.comments.slice(10));
          return false;
        }
      });
    });
    // 楼层回复数据
    this.$nextTick(() => {
      this.$refs.refFloorScroll.scroll.on("pullingUp", async () => {
        this.params.id = this.$route.params.id;
        this.params.time = this.floorData[this.floorData.length - 1].time;
        const data = await getCurFloorComment(this.params);
        this.floorData.push(...data.data.data.comments);

        // /comment/floor?parentCommentId=1438569889&id=29764564&type=0
      });
    });
  },
  provide() {
    return {
      id: this.$route.params.id,
    };
  },
  components: {
    CommentInfo,
    mHeader,
    BetterScroll,
    NavBar
  },
  methods: {
    //获取当前歌曲的评论
    async getMusicComment() {
      const { data } = await getMusicComment({
        id: this.$route.params.id,
        limit: 6,
      });
      //初始化数据
      this.commentData = data.comments;
      this.recommendCommentData = data.comments;
      this.hotCommentData = data.hotComments;
    },
    //显示弹出层 和 子组件传递过来的数据
    hasShowFloor(floorData) {
      this.isFloor = true;
      this.isFloorWarp = true;
      this.$refs.refFloorScroll.scroll.scrollTo(0, 0, 1000);

      this.floorData = floorData.commentsData;
      this.answerCount = floorData.len;

      this.params.parentCommentId = floorData.parentCommentId;
    },
    //隐藏弹出层
    offFloor() {
      this.isFloor = false;
    },
    // 1推荐 和 热门 选择
    handleClick(curindex) {
      this.curIndex = curindex;
      if (curindex == 0) {
        this.commentData = this.recommendCommentData;
        return false;
      }
      this.commentData = this.hotCommentData;
    },
    handleStop(e) {
      e.preventDefault();
    },
    //icon Click
    handleIconClick(){
     this.$router.go(-1)
      this.$store.commit('SET_FULLSCREEN',true)
    },
    //得到当前点击评论的id和姓名
    CurCommitInfo(data){
      console.log(data);
      this.CurInfoName=data.user.nickname;
      this.commentParams.commentId=data.commentId;
    },
    async handleSendComment(){
      const cookie=JSON.parse(localStorage.getItem('cookie'));
      console.log(cookie);
        this.commentParams.id=this.$route.params.id,
        this.commentParams.content=this.CurInfoName
      const {data}= await  sendComment({...this.commentParams,cookie})
      console.log(data);
    }
  },
  created() {
    this.getMusicComment();
  },
};
</script>

