<template>
  <div class="search-page">
    <div class="search-header">
      <span class="icon-search">🔍</span>
      <input
        type="text"
        v-model="keyword"
        @keyup.enter="addToSearch(keyword)"
      />
      <span class="icon-delete" @click="clearKeyWord">❌</span>
      <div class="calcel" @click="$router.go(-1)">取消</div>
    </div>
    <div class="history-page" v-if="!resultShow">
      <scroll class="history-scroll">
        <ul>
          <li class="list-title" v-if="searchText.length">历史搜索</li>
          <li
            class="history-item list-item"
            v-for="historySearchWord in searchText"
            :key="historySearchWord"
            @click.stop="addToSearch(historySearchWord)"
          >
            {{ historySearchWord }}
            <i
              class="iconfont icon-shanchu"
              @click.stop="delKeyWords(historySearchWord)"
            ></i>
          </li>

          <!-- 热门搜索 -->
          <li class="list-title">热门搜索</li>
          <li
            class="list-item"
            v-for="(item, index) in hotWords"
            :key="index"
            @click.stop="addToSearch(item.first)"
          >
            {{ item.first }}
          </li>
        </ul>
      </scroll>
    </div>
    <!-- end -->
    <!-- 搜索结果展示 -->

    <div class="result-page" v-if="resultShow">
      <p class="list-count">共有{{ searchResult.length }}多少首歌曲</p>
      <scroll class="result-scroll">
        <ul>
          <li class="list-item" v-for="item in searchResult" :key="item.id">
            <p>{{ item.name }}</p>
            <!-- <p
              class="artists-name"
              v-for="(artistArr, index) in item"
              :key="index"
            >
              <span v-for="(art, i) in artistArr" :key="i">{{
                art.name
              }}</span> -->
            <!-- </p> -->
          </li>
        </ul>
      </scroll>
    </div>

    <!-- 搜索结果展示 end -->
  </div>
</template>

<script>
import Scroll from "../../components/BetterScroll.vue";
import { mapGetters, mapMutations, mapState } from "vuex";
import { searchHotWords, searchSongApi } from "../../api/requestHome";
export default {
  components: {
    Scroll,
  },
  data() {
    return {
      resultShow: false,
      hotWords: [],
      keyword: "",
      searchResult: [],
    };
  },
  computed: {
    ...mapGetters(["searchText"]),
  },
  created() {
    //请求热门关键词接口函数
    this.searchHotWords();
  },
  mounted() {
    // this.$nextTick(()=>{
    // this.toggleShowResultPage(false)
    // })
  },
  methods: {
    ...mapMutations(["DEL_FROM_SEARCH_TEXT", "SET_SEARCH_TEXT"]),

    toggleShowResultPage(val) {
      this.resultShow = val;
    },

    async searchHotWords() {
      const { data } = await searchHotWords();
      if (data.code === 200) {
        this.hotWords = data.result.hots;
      }
    },
    //输入框关键词搜索请求函数
    async searchSong() {
      if (typeof this.keyword !== "string") {
        return false;
      }
      const { data } = await searchSongApi({ keywords: this.keyword });
      if (data.code === 200) {
        this.searchResult = data.result.songs;
      }
      console.log(data);
    },

    addToSearch(word) {
      console.log(word);
      this.keyword = word;
      this.searchSong();
      if (typeof word === "string") {
        this.SET_SEARCH_TEXT(word);
      }
      this.toggleShowResultPage(true);
    },
    //点击删除当前历史关键词
    delKeyWords(val) {
      console.log("val", val);
      this.DEL_FROM_SEARCH_TEXT(val);
    },
    clearKeyWord() {
      this.keyword = "";
      this.toggleShowResultPage(false);
    },
  },
};
</script>

<style lang="less" scoped>
.search-page {
  width: 100vw;
  height: 100vh;
  .search-header {
    padding: 20px 30px;
    height: 90px;
    background-color: #f2353c;
    display: flex;
    justify-content: space-around;
    align-items: center;
    position: relative;
    .icon-search {
      position: absolute;
      left: 50px;
      margin-right: 50px;
    }
    .icon-delete {
      position: absolute;
      right: 150px;
    }
    input {
      height: 90px;
      font-size: 45px;
      border: 0;
      outline: none;
      border-radius: 20px;
      padding-left: 65px;
      flex: 1;
      box-sizing: border-box;
    }
    .calcel {
      margin-left: 15px;
      font-size: 40px;
      color: white;
    }
  }
  .list-title {
    padding: 0 30px;
    line-height: 100px;
    font-size: 28px;
    color: #989898;
  }
  .list-item {
    padding: 30px;
    font-size: 45px;
    color: #333;
    background-color: white;
    border-bottom: 1px solid #ccc;
  }
  .history-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    .icon-shanchu {
      font-size: 50px;
    }
  }
  .list-count {
    padding: 0 30px;
    line-height: 100px;
    font-size: 36px;
    color: #808080;
    height: 100px;
  }

  .result-scroll {
    height: calc(100vh - 200px);
    overflow: hidden;

    .artists-name {
      margin-top: 10px;
      font-size: 10px;
      color: #b2b2b2;
    }
  }
  .history-scroll {
    height: calc(100vh - 90px);
    overflow: hidden;
  }
}
</style>
