<template>
  <div>
    <m-header>全部推荐</m-header>
    <div class="list-view-warp">
      <PlayListView />
    </div>
  </div>
</template>

<script>
import mHeader from "@/components/mHeader.vue";
import PlayListView from "@/components/PlayListView.vue";

import { getRecommend } from "@/api/requestHome.js";

export default {
  components: { mHeader, PlayListView },
  data() {
    return {
      Recommend: { recommend: [] },
    };
  },
  provide() {
    return {
      projectRecommend: this.Recommend,
    };
  },
  async created() {
    let { data } = await getRecommend();
    if (data.code === 200) {
      this.Recommend.recommend = data.result;
    }
  },
};
</script>

