<template>
  <div class="coment-info-content">
    <ul>
      <li v-for="item in commentData" :key="`${item.commentId}`" @click="handleClick(item)">
        <img :src="item.user.avatarUrl" alt="" />
        <div class="comment-info">
          <div class="user-info">
            <div class="name">{{ item.user.nickname }}</div>
            <div class="time">{{ item.timeStr }}</div>
          </div>
          <div class="cur-comment">{{ item.content }}</div>
          <FloorAnswer
            @emitCurrentAnswer="emitCurrentAnswer($event)"
            :parentCommentId="item.commentId"
          />
        </div>
        <div class="count">
          <i class="iconfont icon-iconfontzhizuobiaozhun023148"></i
          >{{ item.likedCount }}
        </div>
      </li>
    </ul>

    <!-- floor -->
  </div>
</template>

<script>
import FloorAnswer from "./FloorAnswer.vue";
export default {
  props: {
    commentData: {
      type: Array,
    },
  },
  inject: ["id"],
  components: {
    FloorAnswer,
  },
  methods: {
    stopHandle() {
      return false;
    },
    emitCurrentAnswer(floorData) {
      this.$emit("emitFloorData", floorData);
    },
    handleClick(curItem){
      this.$emit('CurCommitInfo',curItem)
    }
  },
};
</script>

