<template>
  <div class="hotsonger common-home">
    <div class="title-info">
      <div class="title">热门歌手</div>
      <div @click="goToArtists()">🔥</div>
    </div>
    <ul class="artists-content">
      <li v-for="(item, index) in artists" :key="index" @click="goToArtistInfo(item)">
        <img v-lazy="`${item.picUrl}?param=400y400`" alt="" />
        <div class="info">
          <div class="name">{{ item.name }}</div>
          <div class="dec">
            有{{ item.albumSize }}专辑 ，共{{ item.musicSize }}首歌
          </div>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: "hotsonger",
  props: {
    artists: {
      type: Array,
      default() {
        return [];
      },
    },
  },
  methods:{
    //歌手分类列表
    goToArtists(){
      this.$router.push({
        name:'artists'
      })
    },
    goToArtistInfo(item){
      this.$router.push({
        name:'ArtistInfo',
        params:{
          id:item.id
        }
      })
    }
  }
};
</script>

