<template>
  <div class="icons-content common-home">
    <swiper ref="mySwiper" :options="swiperOptions">
      <swiper-slide v-for="(curIcons, index) in pages" :key="index">
        <div
          class="icon-warp"
          v-for="(icon, index) in curIcons"
          :key="index"
          @click="handleCurIcon(icon)"
        >
          <div class="img-warp">
            <img :src="icon.iconUrl" alt="" />
          </div>
          <span class="title">{{ icon.name }}</span>
        </div>
      </swiper-slide>
    </swiper>
  </div>
</template>

<script>
export default {
  props: {
    icons: {
      type: Array,
      require: true,
      default() {
        return [];
      },
    },
  },
  data() {
    return {
      swiperOptions: {
        pagination: {
          el: ".swiper-pagination", //与slot="pagination"处 class 一致
        },
      },
    };
  },
  methods: {
    handleCurIcon(curItem) {
      switch (curItem.name) {
        case "每日推荐":
          this.$router.push({
            name: "recommendView",
          });
          break;
            case "私人FM":
          this.$router.push({
            name: "playListView",
          });
          break;
        case "歌单":
          this.$router.push({
            name: "artists",
          });
          break;
        case "排行榜":
          this.$router.push({
            name: "rank",
          });
          break;

        default:
          return false;
          break;
      }
    },
  },
  computed: {
    pages() {
      let pages = [];
      this.$props.icons.forEach((curItem, index) => {
        let page = Math.floor(index / 4);
        if (!pages[page]) {
          pages[page] = [];
        }
        pages[page].push(curItem);
      });
      pages.length = 2;

      return pages;
    },
  },
};
</script>

<style lang="less" scoped>
.icons-content {
  .swiper-slide {
    display: flex;
    justify-content: space-around;
    .icon-warp {
      display: flex;
      flex-direction: column;
      align-items: center;
      .title {
        font-size: 18px;
      }
      .img-warp {
        width: 100px;
        height: 100px;
        background: red;
        border-radius: 50%;
        margin-bottom: 10px;
        img {
          width: 100%;
          height: 100%;
        }
      }
    }
  }
}
</style>